* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    color: #333;
    background-color: #f4f4f4;
    padding: 20px;
}

.resume {
    max-width: 800px;
    margin: 0 auto;
    background-color: white;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    padding: 20px;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 20px;
    border-bottom: 4px solid #ffd700;
    padding-bottom: 20px;
}

.name-title {
    text-align: left;
}

.contact-info {
    text-align: right;
}

h1 {
    font-size: 2.5em;
    margin-bottom: 5px;
}

h2 {
    font-size: 1.5em;
    color: #666;
}

main {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.resume-section {
    display: flex;
    border-bottom: 1px solid #000;
    padding-bottom: 20px;
}

h3 {
    font-size: 1.2em;
    margin-bottom: 10px;
    width: 200px;
    flex-shrink: 0;
}

.section-content {
    flex-grow: 1;
}

.key-skills ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-top: 10px;
}

.job, .degree {
    margin-bottom: 15px;
}

h4 {
    font-size: 1.1em;
    margin-bottom: 5px;
}

.date {
    font-style: italic;
    color: #666;
    margin-bottom: 5px;
}

@media (max-width: 600px) {
    header {
        flex-direction: column;
        align-items: center;
    }

    .contact-info {
        text-align: center;
        margin-top: 10px;
    }

    .resume-section {
        flex-direction: column;
    }

    h3 {
        width: 100%;
        margin-bottom: 10px;
    }

    .key-skills ul {
        grid-template-columns: 1fr;
    }
}